import React, { useState, useEffect } from "react";
import Navbar from "../../Navbar/Navbar";
import { GApageView, Event } from "../../Tracking/index";
import { Link } from "react-router-dom";
import "./Adoptera.css";

const Adoptera = () => {
  useEffect(() => {
    GApageView("adoptera");
  }, []);

  const startQuiz = () => {
    Event("QUIZ", "Startade quiz", "QUIZ-startat");
  };
  return (
    <>
      <Navbar black />
        <div className="headerWizard">
          <h2>Låt vårt matchningsverktyg <br /> hitta din pawfect match</h2>
          <Link to="/adoptera" onClick={startQuiz}>
            <button className="button">
              Starta testet
            </button>
          </Link>
        </div>
    </>
  );
};

export default Adoptera;
